import React from "react";
import { render } from "react-dom";
import {createColumns, createTable } from "./Utils";

// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";

function TTable(props) {

    return <ReactTable
        data = { props.data }
        columns = { props.columns }
    />
}

let eq = "a&b"

class App extends React.Component {
    constructor() {
        super();
        this.state = {
            data: createTable(eq),
            eq: eq,
            cols: createColumns(eq).map((key)=>{
                return {
                    Header: key,
                    accessor: key
                }
            })
        };
        //this.changeEquation = this.changeEquation.bind(this);
        this.handleClick = this.handleClick.bind(this)
    }

    handleClick(event)
    {
        const { eq } = this.state;
        this.setState({data: createTable(eq)});
        let columns = createColumns(eq).map((key)=>{
                return {
                    Header: key,
                    accessor: key
                }
            });
        this.setState({cols: columns});
        event.preventDefault()
    }

    render(){
        let { data } = this.state;
        let { cols } = this.state;

        return <div>
            <div class="container">
            <form onSubmit={this.handleClick}>
                <input type="text" value={this.state.eq} onChange={event => this.setState({eq: event.target.value})}/>
                <input type="submit" value="Submit" />
            </form>
                </div>
            <TTable
                data = { data }
                columns = { cols }
            />
        </div>
    }

}

render(<App/>, document.getElementById("root"));



